
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-npx:after {
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
border-npx:after {
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
border-npx:after {
    transform: scaleY(0.33);
}
}
.all li {
  width: 100%;
  box-sizing: border-box;
  padding: 0 10px;
  height: 60px;
  position: relative;
}
.all li:after {
  display: block;
  content: '';
  width: 100%;
  height: 1px;
  background-color: #ccc;
  position: absolute;
  bottom: 0;
  left: 0;
}
.all li .title {
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  height: 24px;
  line-height: 24px;
  font-size: 14px;
}
.all li .title span {
  display: inline-block;
  border-radius: 2px;
  margin: 2px;
  padding: 0 3px;
  height: 20px;
  line-height: 20px;
  color: #fff;
}
.all li .title span.top {
  background: #e74c3c;
}
.all li .title span.ask {
  background: #3498db;
}
.all li .title span.good {
  background: #e67e22;
}
.all li .title span.job {
  background: #9b59b6;
}
.all li .title span.share {
  background: #1abc9c;
}
.all li .detail img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}


